@media (max-width: 870px) {
  .header__menu {
    z-index: 15;
  }  
  
  .navigation {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateY(-100%);
    background: $black;
    z-index: 10;

    &-active {
      transform: translateY(0);
    }

    &__list {
      flex-direction: column;
      padding: 120px 0;
      li {
        height: 59px;
      }

      &-item {

        &:before {
          transform: translateX(calc(-100% -1px));
        }
        &:hover{
          transform: translateX(0);
        }
        &:hover:before {

        }
      }
    }
  }

  .main-block__intro{
    &-heading {
      font-size: 48px;
    }
    &-tags-item {
      font-size: 18px;
    }
  }
}

