.footer {
  padding:60px 0;
  background: $darkGray;

  .social-media {
    display: flex;
    justify-content: center;
    align-items:center;

    li {
      padding: 1px 12px;
      &:not(:last-child) {border-right: 1px solid rgba($white, 0.5)}
    }
    a {
      width: 14px;
      max-height: 14px;
    }
  }
}
